import { Link, useLoaderData, LoaderFunction } from "remix";
import { getPosts } from "~/lib/post";
import type { Post } from "~/lib/post";

export const loader: LoaderFunction = () => {
  // throw new Error("An error occurred");
  return getPosts();
};

