export default function Posts() {
  const posts: Post[] = useLoaderData();
  const apiKey = JSON.stringify(process.env.API_KEY);
  console.log(posts);

  return (
    <div className="p-10">
      <h1 className="text-3xl font-normal leading-normal mt-0 mb-2">Posts</h1>
      {apiKey}
      {posts.map((post) => (
        <div key={post.slug}>
          <Link to={post.slug}>{post.title}</Link>
        </div>
      ))}
    </div>
  );
}
